@import "~styles/variables";

.launches-list {
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: $almost-black;
    min-height: 100vh;

    &__top {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-direction: column;
        height: 50vh;
        background: url('../../assets/img/moon.png') no-repeat;
        background-size: 80%;
        background-position: top center; }

    &__text {
        text-transform: uppercase;
        @include header_sml;
        letter-spacing: .8em;
        color: $cool-gray; }

    &__logo {
        width: 50%; }

    &__launches {
        width: 80vw;
        display: flex;
        flex-direction: column;

        &:nth-child(odd) {
            align-self: flex-end; } }

    &__error {
        padding: 1em;
        @include basic;
        text-transform: uppercase;
        color: $white;
        text-align: center; } }

.launch {
    padding: 2em 0;
    width: 50%;
    text-transform: uppercase;
    color: $white;
    @include basic;
    cursor: pointer;

    &:nth-child(even) {
        align-self: flex-end; }

    &__date {
        letter-spacing: .8em; }

    &__list {
        padding: 0;
        margin: 0;
        list-style-type: none;
        display: flex;
        flex-direction: row; }

    &__arrow {
        margin: 1em 0;
        height: 3px;
        width: 100%;
        background-color: $desert; }

    &__list-item {
        padding-right: 1em; }

    &__name {
        color: $cool-gray; } }
