@import "~styles/variables";

.launch-details {

    &__header {
        padding: 2em 4em;
        background-color: $almost-black;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative; }

    &__go-back-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        background-color: transparent;
        border: none;
        color: $desert;
        text-transform: uppercase;
        @include basic;
        letter-spacing: .8em;
        position: absolute;
        top: 2em;
        left: 1em;
        cursor: pointer;

        &::before {
            content: '';
            height: 10px;
            width: 30px;
            background: url('../../assets/img/arrow_pointer.png') no-repeat; } }

    &__logo {
        height: 35px; }

    &__start-date {
        @include basic;
        color: $cool-gray;
        text-transform: uppercase; }

    &__rocket-name {
        margin-bottom: 0;
        @include header_big;
        text-transform: uppercase; }

    &__countdown {
        @include basic;
        text-transform: uppercase; }

    &__mission-patch {
        display: block;
        padding: 5em;
        max-width: 300px; }


    &__columns {
        padding: 2em 4em;
        display: flex; }

    &__first-column {
        padding: 2em;
        width: 50%; }

    &__second-column {
        padding: 2em;
        width: 50%; } }

.mission-links {
    padding: 2em 4em;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    background: url('../../assets/img/rocket.png') no-repeat;
    background-position: center;
    background-size: cover;
    min-height: 600px;

    &__list {
        padding: 0;
        display: flex;
        list-style-type: none;
        margin-bottom: 10em; }


    &__heading {
        text-transform: uppercase;
        color: $white;
        @include header_sml;
        letter-spacing: .9em; }

    &__link {
        padding: .5em 2em;
        margin: 1em;
        color: $white;
        text-transform: uppercase;
        text-decoration: none;
        border: 1px solid $white;
        border-radius: 100px;
        @include basic;

        &:hover {
            opacity: 0.7; } } }
